/** @jsx React.DOM */

var React = require('react'),
	Col = require('react-bootstrap/Col'),
	Link = require('react-router').Link;

module.exports = React.createClass({
	displayName: "Logo",

	render: function () {
		return (
			React.createElement(Link, {to: "app"}, 
				React.createElement("img", {src: "img/logo.png"})
			)
		);
	}
});