/** @jsx React.DOM */

var React = require('react'),
	Container = require('react-bootstrap/Grid'),
	Col = require('react-bootstrap/Col'),
	Input = require('react-bootstrap/Input'),
	Icon = require('../helper/icon.jsx'),
	Button = require('react-bootstrap/Button'),
	DropdownButton = require('react-bootstrap/DropdownButton'),
	MenuItem = require('react-bootstrap/MenuItem');

module.exports = React.createClass({
	displayName: "SearchInput",

	getDefaultProps: function () {
		return {
			countries: {
				"dub": "Dubai",
				"aac": "Aachen",
				"ber": "Berlin"
			},
			default: "dub"
		}
	},


	render: function () {
		var searchAddon = (
			React.createElement(Button, {className: "searchButton", bsStyle: "link"}, 
				React.createElement(Icon, {name: "search-icon"})
			)
		);

		var title = this.props.countries[this.props.default];

		var items = [];

		for (var country in this.props.countries) {
			if (this.props.countries.hasOwnProperty(country)) {
				items.push(React.createElement(MenuItem, {key: country, eventKey: country}, this.props.countries[country]));
			}
		}

		var buttonBefore = (
			React.createElement(DropdownButton, {onClick: this.onClick, key: "countrySelect", title: title, className: "selectpicker bla bli"}, 
						items
			)
		);

		return (
			React.createElement(Input, {
				type: "text", 
				buttonBefore: buttonBefore, 
				addonAfter: searchAddon}
			)
		);
	}
});