/** @jsx React.DOM */

var React = require("react"),
	Router = require("react-router"),
	Route = Router.Route,
	DefaultRoute = Router.DefaultRoute;

var Root = require("./components/root.jsx"),
	Login = require("./components/login.jsx"),
	App = require("./components/app.jsx"),
	Home = require("./components/home/home.jsx"),
	Simple = require("./components/misc/simple.jsx");

module.exports = (
	React.createElement(Route, {name: "root", path: "/", handler: Root}, 
		React.createElement(Route, {name: "app", path: "/", handler: App}, 
			React.createElement(Route, {name: "offers", handler: Home}), 
			React.createElement(Route, {name: "requests", handler: Simple}), 
			React.createElement(Route, {name: "map", handler: Simple}), 
			React.createElement(DefaultRoute, {handler: Home})
		), 
		React.createElement(Route, {name: "login", handler: Login})
	)
);

